::placeholder {
  color: #666;
}

::selection {
  color: #fff;
}

::-webkit-scrollbar {
  width: 0;
  /* remove scrollbar space */
  background: transparent;
  /* optional: just make scrollbar invisible */
}

/* optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: #ff0000;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  overflow-x: hidden;
  font-size: 16px;
}

body,
html {
  width: 100%;
  height: 100%;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 320px;
  font-family: Georgia, "Times New Roman", serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.6rem;
  -webkit-font-smoothing: antialiased;
  color: #000;
  overflow-x: hidden;
}

.wrapper {
  flex: 1 0 auto;
  position: relative;
  width: 100%;
}
.wrapper_padding-bottom {
  padding-bottom: 100px;
}

.container {
  max-width: 1200px;
  height: 100%;
  box-sizing: border-box;
  margin: 0 auto;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

input,
textarea {
  outline: none;
}
input:focus:required:invalid,
textarea:focus:required:invalid {
  border-color: red;
}
input:required:valid,
textarea:required:valid {
  border-color: green;
}

li {
  list-style: none;
}

ul {
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
}

@media screen and (max-width: 1200px) {
  section,
  header,
  footer {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 480px) {
  section,
  header,
  footer {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.section__title {
  margin: 0;
  font-size: 35px;
  line-height: 130%;
  font-weight: 400;
  letter-spacing: 0.06em;
  text-align: left;
  color: #545454;
}
@media screen and (max-width: 480px) {
  .section__title {
    width: 100%;
    font-size: 25px;
    line-height: 32px;
    letter-spacing: 1px;
    word-spacing: 3px;
    text-align: center;
  }
}

.section__title--center {
  text-align: center;
}

.section__title--white {
  color: #ffffff;
}

.hidden {
  display: none;
  position: absolute;
  top: -9999px;
  left: -9999px;
  -webkit-clip-path: polygon(0px 0px, 0px 0px, 0px 0px, 0px 0px);
          clip-path: polygon(0px 0px, 0px 0px, 0px 0px, 0px 0px);
}

.btn {
  padding: 16px 45px;
  font-size: 17px;
  letter-spacing: 0.06em;
  color: #292929;
  background-color: #f1cdb3;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: 0.3s;
}
.btn:hover {
  background-color: #fddcc4;
  background-size: 0 100%;
}
.btn--transparent {
  background-color: transparent;
  border: 2px solid #f1cdb3;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.btn--transparent:hover {
  border-color: #fddcc4;
}
.gallery-item .btn {
  padding: 13px 45px;
}
@media screen and (max-width: 768px) {
  .hero__left .btn {
    align-self: center;
  }
}

