/* =========== Header ==========*/
.header {
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  z-index: 1000;
}
@media screen and (max-width: 768px) {
  .header {
    top: 30px;
  }
}
@media screen and (max-width: 700px) {
  .header {
    padding: 0;
  }
}
.inner-page .header {
  position: sticky;
  top: 0;
  left: 0;
  transform: none;
  height: 120px;
  background: #ffffff;
  transition: all 0.3s;
}
.inner-page .header.header--scroll {
  box-shadow: 0 0 11px #ddd;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.inner-page .header__wrapper {
  height: 100%;
}

.header__logo {
  position: relative;
  left: 0;
  right: auto;
  transition: 0.3s;
}
@media screen and (max-width: 700px) {
  .header__logo {
    left: 30px;
  }
}
@media screen and (max-width: 480px) {
  .header__logo {
    left: 10px;
  }
}
.header__logo--mobile-menu-open {
  top: 0;
  z-index: 2;
  left: calc(100% - 300px);
}

.logo__title {
  margin-bottom: 10px;
  font-size: 32px;
  line-height: 36px;
  letter-spacing: 0.06em;
  color: #f1cdb3;
  transition: 0.3s;
}
.inner-page .header__logo--mobile-menu-open .logo__title {
  color: #f1cdb3;
}
.logo__title a {
  color: inherit;
}
.inner-page .logo__title {
  color: #545454;
}

.logo_subtitle {
  font-family: Arial, sans-serif;
  font-size: 13px;
  line-height: 15px;
  letter-spacing: 0.1em;
  text-align: center;
  color: #ffffff;
  transition: 0.3s;
}
.inner-page .logo_subtitle {
  color: #545454;
}
.inner-page .header__logo--mobile-menu-open .logo_subtitle {
  color: #ffffff;
}

/* /. =========== Header ==========*/
/* ======== Menu ========== */
.nav.mobile-open {
  position: fixed;
  max-width: 320px;
  height: 100vh;
  top: -30px;
  right: 0;
  bottom: 0;
  background: #292929;
  display: flex;
  justify-content: center;
  align-content: center;
}
.inner-page .nav.mobile-open {
  top: 0;
}

.nav__mobile-toggle {
  position: relative;
  right: 43px;
  width: 30px;
  height: 22px;
  display: none;
  transition: 0.3s;
}
.inner-page .nav__mobile-toggle {
  top: 50px;
}
@media screen and (max-width: 700px) {
  .nav__mobile-toggle {
    position: absolute;
    z-index: 100000;
    top: 19px;
    right: 54px;
    display: block;
  }
}
.nav__mobile-toggle span {
  position: absolute;
  left: 0;
  width: 30px;
  height: 2px;
  display: inline-block;
  background: #f1cdb3;
  transition: all 0.3s;
  opacity: 1;
}
.nav__mobile-toggle span:nth-child(1) {
  top: 0;
}
.nav__mobile-toggle span:nth-child(2) {
  top: 50%;
}
.nav__mobile-toggle span:nth-child(3) {
  top: 50%;
}
.nav__mobile-toggle span:nth-child(4) {
  bottom: 0;
}
.nav__mobile-toggle:hover span:nth-child(1) {
  top: 0;
  left: -5px;
}
.nav__mobile-toggle:hover span:nth-child(2) {
  top: 50%;
}
.nav__mobile-toggle:hover span:nth-child(3) {
  top: 50%;
}
.nav__mobile-toggle:hover span:nth-child(4) {
  bottom: 0;
  left: 5px;
}
.nav__mobile-toggle.open {
  height: 22px;
  position: absolute;
  top: 19px;
  right: 53px;
  transform: rotate(90deg);
}
.inner-page .nav__mobile-toggle.open {
  top: 50px;
}

.menu {
  display: flex;
  justify-content: flex-end;
  margin-top: -7px;
  transition: opacity 0.3s 1s;
}
.inner-page .menu {
  top: 0;
}
@media screen and (max-width: 700px) {
  .menu {
    position: absolute;
    top: -25px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 102vh;
    width: 320px;
    padding-top: 117px;
    opacity: 1;
    background: #292929;
    transform: translateX(320px);
    transition: all 0.3s;
  }
}

.menu-mobile {
  opacity: 1;
  transform: translateX(0);
}

.menu__item {
  margin-right: 36px;
}
@media screen and (max-width: 700px) {
  .menu__item {
    margin: 0 0 40px;
  }
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  position: relative;
  font-family: Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 160%;
  color: #cdcdcd;
  transition: 0.3s;
}
@media screen and (max-width: 700px) {
  .menu__link {
    margin: 0;
    font-size: 32px;
  }
}
.inner-page .menu-mobile .menu__link {
  color: #cdcdcd;
}
.inner-page .mobile-open .menu__link {
  color: #cdcdcd;
}

.menu__link.active,
.menu__link:hover {
  color: #ffffff;
}
.inner-page .menu__link.active,
.inner-page
  .menu__link:hover {
  color: #292929;
}
.inner-page .menu-mobile .menu__link.active,
.inner-page .menu-mobile
  .menu__link:hover {
  color: #ffffff;
}

.menu__link.active::after {
  content: "";
  position: absolute;
  bottom: -7px;
  left: 0;
  width: 100%;
  height: 3px;
  border-radius: 3px;
  background-color: #f1cdb3;
  transform: scaleX(0);
  transition: 0.3s;
}

.menu__link.active::after,
.menu__link:hover::after {
  transform: scaleX(1);
}

